////////////////////////// LOGOS //////////////////////////
.logos {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 300px;
    height: 50px;

    .logo__line {
        height: 50px;
        mix-blend-mode: normal;
        border-right: 1px solid #fff;
        opacity: 0.5;
    }
    .travelperk svg {
        width: 100%;
        height: auto;
    }
    .logo__lola img {
        width: 90%;
        height: auto;
    }
}

@media screen and (max-width: 768px) {
    .logos {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 300px;
        height: 50px;
    }
}
@media screen and (min-width: 992px) {}
